// Magnetic Button Component (මෙය නොවෙනස්ව පවතී)
const MagneticButton = ({ children, ...props }: any) => {
    const ref = useRef<HTMLButtonElement>(null);
    const textRef = useRef<HTMLSpanElement>(null);

    useEffect(() => {
        const button = ref.current;
        const text = textRef.current;
        if (!button || !text) return;

        const onMouseMove = (e: MouseEvent) => {
            const { clientX, clientY } = e;
            const { left, top, width, height } = button.getBoundingClientRect();
            const x = clientX - (left + width / 2);
            const y = clientY - (top + height / 2);
            gsap.to(button, { x: x * 0.4, y: y * 0.4, duration: 0.8, ease: "power3.out" });
            gsap.to(text, { x: x * 0.2, y: y * 0.2, duration: 0.8, ease: "power3.out" });
        };

        const onMouseLeave = () => {
            gsap.to(button, { x: 0, y: 0, duration: 0.8, ease: "elastic.out(1, 0.3)" });
            gsap.to(text, { x: 0, y: 0, duration: 0.8, ease: "elastic.out(1, 0.3)" });
        };

        button.addEventListener('mousemove', onMouseMove);
        button.addEventListener('mouseleave', onMouseLeave);

        return () => {
            button.removeEventListener('mousemove', onMouseMove);
            button.removeEventListener('mouseleave', onMouseLeave);
        };
    }, []);

    return (
        <button ref={ref} {...props}>
            <span ref={textRef}>{children}</span>
        </button>
    );
};

